/* src/components/form-field/form-field.css */
.form-field {
  font: var(--font-stack-headline);
  display: block;
  min-width: 0;
  min-height: 50px;
  max-width: 500px;
  padding: 0 var(--spacing-md);
  border-radius: var(--radius-lg);
  background-color: var(--color-gray);
  border: none;
  color: var(--color-white);
  font-size: 16px;
}
.form-field::placeholder {
  color: var(--color-light-gray);
  font-weight: var(--font-weight-light);
}
.form-field:focus {
  outline: none;
}
input[type=date] {
  color: var(--color-light-gray);
}
.input-wrapper {
  width: 100%;
  position: relative;
  box-sizing: border-box;
}
.input-wrapper.tall-field {
  height: 80px;
}
.input-wrapper.tall-field .input-field {
  height: 100%;
  resize: none;
  padding-top: 12px;
}
.input-with-toggle {
  position: relative;
  display: flex;
  align-items: center;
}
.date-input {
  color: transparent;
}
.date-input:not(.has-value)::-webkit-datetime-edit,
.date-input:not(.has-value)::-webkit-datetime-edit-text,
.date-input:not(.has-value)::-webkit-datetime-edit-month-field,
.date-input:not(.has-value)::-webkit-datetime-edit-day-field,
.date-input:not(.has-value)::-webkit-datetime-edit-year-field {
  color: transparent;
}
.date-input.has-value {
  color: var(--color-white);
}
.date-input.has-value::-webkit-datetime-edit,
.date-input.has-value::-webkit-datetime-edit-text,
.date-input.has-value::-webkit-datetime-edit-month-field,
.date-input.has-value::-webkit-datetime-edit-day-field,
.date-input.has-value::-webkit-datetime-edit-year-field {
  color: var(--color-white);
}
.date-placeholder {
  position: absolute;
  left: var(--spacing-md);
  top: 50%;
  transform: translateY(-50%);
  color: var(--color-light-gray);
  font-weight: var(--font-weight-light);
  pointer-events: none;
}
.input-with-toggle.with-toggle .input-field {
  padding-right: 42px;
}
.toggle-btn {
  position: absolute;
  right: 10px;
  background: transparent;
  border: none;
  color: #bbbbbb;
  cursor: pointer;
  display: grid;
  place-items: center;
  padding: 4px;
}
.toggle-btn mat-icon {
  font-size: 20px;
  width: 20px;
  height: 20px;
}
.input-field {
  width: 100%;
  height: 50px;
  padding: 0 var(--spacing-md);
  background-color: var(--color-gray);
  border: 2px solid rgba(255, 255, 255, 0.12);
  border-radius: var(--radius-lg);
  color: var(--color-white);
  font-size: 16px;
  font-family: inherit;
  font-weight: var(--font-weight-light);
  outline: none;
  box-sizing: border-box;
  transition: border-color 0.2s;
}
.input-field:focus {
  border-color: var(--color-accent);
}
.input-field::placeholder {
  color: var(--color-light-gray);
}
.input-wrapper.error .input-field {
  border-color: var(--color-error);
}
input:-webkit-autofill,
input:-webkit-autofill:hover,
input:-webkit-autofill:focus,
input:-webkit-autofill:active {
  -webkit-box-shadow: 0 0 0 30px var(--color-gray) inset !important;
  -webkit-text-fill-color: var(--color-white) !important;
  transition: background-color 5000s ease-in-out 0s;
  caret-color: var(--color-white);
}
/*# sourceMappingURL=form-field.css.map */
